.Container {
    position: relative;
    min-height: 800px;
    max-height: 800px;
    overflow: hidden;
    margin-bottom: 0px;
    /* background-color: black; */
}

/* Style the video: 100% width and height to cover the entire window */
.Video {
    width: 100%;
    height: 100%;
  }
  
  /* Add some content at the bottom of the video/page */
  .Content {
    position: absolute;
    top: 0;
    color:#ffffff;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    /* background-color: rgba(44, 0, 30, 0.7); */
    background-color: rgba(46, 2, 71, 0.6);
  }
  .SubContent {
    font-size: large;
      text-align: center;
      padding: 0px;
      /* color: rgb(140, 218, 104); */
      color: rgb(23, 250, 117);
    }
  .SubContent h1 {
    font-size: 100px;
    text-align: left;
  }
  .SubContent h2 {
    font-size: 28px;
    text-align: left;
  }
